import React from 'react';
import { Link } from 'react-router-dom';




const ExpenseListItem=({description, amount, createdAt, id})=>(
    <div>
    <Link to={`/edit/${id}`}>
    <h3>description: {description}</h3>
    </Link>
    <p>amount: {amount}</p>
    <p>created at: {createdAt}</p>
    </div>
)

export default ExpenseListItem